
.card {
    display: flex;
    /* width: calc(50% - 60px); */
    flex: 1 1 400px;
    margin: 30px;
    background-color: #f9f9f9;
    justify-content: center;
    box-shadow: 10px 5px 5px rgb(0, 0, 0, 0.3);
}


.card a {
    color: #000;
    display: flex;
    text-decoration: none;
    position: relative;
    padding: 10px 10px;
    /* margin: 20px 50px; */
   
    font-size: 16px;
}



.card a p {
    margin-top: 10px;
}




.card-top {
    position: relative;
    min-width: 200px;
    height: 250px;
    position: relative;
    top: -50px;
    margin-right: 20px;

}
.card-top img::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background-image: url("./not-found.jpg");
    background-size: cover;
    background-position:center;
}
