    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
} */

@media (max-width: 970px) {
    .card {
        min-width: 100%;
        padding: 0;
        margin: 40px 0;
    }
    .card-top {
        margin-bottom: 30px;
    }
    .card a {
        flex-direction: column;
        align-items: center;
        
    }
    .card a p {
        text-align: center;
        margin-top: 20px;
    }
    
}