.nav {
    display: flex;
    background-color: #f9f9f9;
    justify-content: flex-start;

    
}

.nav a {
    text-decoration: none;
    color: #000000;
    font-size: 24px;
    padding: 15px;
    margin: 10px;
    transition: all .3s;
}

.nav a:hover {
    color: #778899;
    
}